import React, { HTMLAttributes } from 'react';
import cn from 'classnames';

import css from './Icon.css';

export type IconProps = HTMLAttributes<HTMLSpanElement> & {
  className?: string;
  size?: 'xs' | 'sm' | 'md' | 'lg';
  color?: 'black' | 'green' | 'grey' | 'greyDark' | 'white' | 'orange';
};

export const Icon = ({
  children,
  className = '',
  size = 'md',
  color,
  ...props
}: IconProps) => (
  <span
    className={cn(css.icon, className, css[size], color && css[color])}
    {...props}
  >
    {children}
  </span>
);
