import React, { HTMLAttributes } from 'react';
import cn from 'classnames';

import css from './Typography.css';
import { MarkdownComponent, MarkdownProps } from './Markdown';

export type TypographyProps = {
  children: React.ReactNode;
  color?: 'black' | 'green' | 'grey' | 'greyDark' | 'white' | 'orange';
  className?: string;
} & HTMLAttributes<HTMLDivElement>;

export const Heading1: React.FC<TypographyProps> = ({
  children,
  className,
  color = 'black',
}) => {
  return (
    <h1 className={cn(css.heading1, css[color], className)}>{children}</h1>
  );
};

export const Heading2: React.FC<TypographyProps> = ({
  children,
  className,
  color = 'black',
}) => {
  return (
    <h2 className={cn(css.heading2, css[color], className)}>{children}</h2>
  );
};

export type ParagraphProps = TypographyProps & {
  width?: 'normal' | 'bold';
};

export const Paragraph: React.FC<ParagraphProps> = ({
  children,
  className,
  color = 'black',
  width = 'normal',
}) => {
  return (
    <div className={cn(css.paragraph, css[color], css[width], className)}>
      {children}
    </div>
  );
};

export type ParagraphMarkdownProps = Omit<ParagraphProps, 'className'> &
  MarkdownProps;

export const TextMarkdown: React.FC<ParagraphMarkdownProps> = ({
  color,
  width,
  className,
  children,
  ...markdownProps
}) => (
  <Paragraph color={color} width={width} className={className}>
    <MarkdownComponent {...markdownProps} children={children} />
  </Paragraph>
);

export const Footnote: React.FC<ParagraphProps> = ({
  children,
  className,
  width = 'nornal',
  color = 'black',
}) => {
  return (
    <div className={cn(css.footnote, css[color], css[width], className)}>
      {children}
    </div>
  );
};

export const Caption: React.FC<ParagraphProps> = ({
  children,
  color = 'black',
  width = 'bold',
  className,
  ...props
}) => {
  return (
    <div
      {...props}
      className={cn(css.caption, css[color], css[width], className)}
    >
      {children}
    </div>
  );
};
