import React, { AnchorHTMLAttributes } from 'react';
import Markdown, { MarkdownToJSX } from 'markdown-to-jsx';
import cn from 'classnames';

import css from './Markdown.css';
import typographyCss from './Typography.css';

export type MarkdownProps = MarkdownToJSX.Options & {
  [key: string]: any;
  linkTarget?: string;
  linkClassName?: string;
  linkColor?: 'black' | 'green' | 'grey' | 'greyDark' | 'white' | 'orange';
  children: string;
};

const LinkMarkdown = ({
  children,
  title,
  ...props
}: AnchorHTMLAttributes<HTMLAnchorElement>) => <a {...props}>{children}</a>;

export const MarkdownComponent = ({
  className,
  children,
  linkTarget,
  linkClassName,
  linkColor = 'green',
  ...props
}: MarkdownProps) => {
  const text = children.replace(/\\n/g, '\n');

  return (
    <Markdown
      children={text}
      className={cn(css.markdown, className)}
      options={{
        ...props,
        overrides: {
          a: {
            component: LinkMarkdown,
            props: {
              className: cn(typographyCss[linkColor], css.link, linkClassName),
              target: linkTarget,
            },
          },
        },
      }}
    />
  );
};
