import React, { useState, useEffect, useCallback } from 'react';

import { toCamelCase } from './utils/toCamelCase';

import { Icon } from './Icon';
import type { IconProps } from './Icon';

import * as countries from './icons/countries';
import * as commons from './icons/common';
import type { IconCommonName } from './icons/common';
import type { IconCountryName } from './icons/countries';

const SLASH = '/';

type OwnProps = {
  name: IconCommonName | IconCountryName;
  onError?: () => void;
};

export type IconLoaderProps = OwnProps & IconProps;

export const IconLoader = ({
  name: fullPath,
  onError = () => {},
  ...props
}: IconLoaderProps) => {
  const [iconsLoaded, setIconsLoaded] = useState(false);

  const getName = (fullPath: string) =>
    toCamelCase(fullPath.split(SLASH).slice(-1)[0]);
  const getNamespace = (fullPath: string) =>
    fullPath.split(SLASH).slice(0, -1).join(SLASH);

  const namespace = getNamespace(fullPath);
  const name = getName(fullPath);

  const loadIcons = useCallback(() => {
    if (namespace && typeof IconLoader.namespaces[namespace] === 'function') {
      IconLoader.namespaces[namespace]()
        .then(() => setIconsLoaded(true))
        .catch(onError);
    } else if (!namespace || !IconLoader.namespaces[namespace]) {
      onError();
    } else {
      setIconsLoaded(true);
    }
  }, [namespace, onError]);

  useEffect(() => {
    setIconsLoaded(false);
    loadIcons();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [namespace]);

  if (
    iconsLoaded &&
    IconLoader.namespaces[namespace] &&
    IconLoader.namespaces[namespace][name]
  ) {
    const IconComponent = IconLoader.namespaces[namespace][name];

    return (
      <Icon {...props}>
        <IconComponent />
      </Icon>
    );
  }

  return null;
};

IconLoader.namespaces = {} as Record<string, any>;

IconLoader.addIcons = (namespace: string, promise: any) => {
  IconLoader.namespaces[namespace] = Object.keys(promise).reduce((acc, k) => {
    acc[toCamelCase(k)] = promise[k];
    return acc;
  }, {} as any);
};

IconLoader.addIcons('icon:core/countries', countries);
IconLoader.addIcons('icon:core/common', commons);
