import React, { FC, ReactNode } from 'react';

import css from './indent.css';

export const INDENT = {
  none: 'none',
  x: 'x',
  s: 's',
  m: 'm',
  l: 'l',
  xl: 'xl',
} as const;

export const VERTICAL_MARGIN_DIRECTION = {
  both: 'both',
  top: 'top',
  bottom: 'bottom',
} as const;

export type Indent = keyof typeof INDENT;

export type VerticalMarginDirection = keyof typeof VERTICAL_MARGIN_DIRECTION;

export type IndentWrapperProps = {
  verticalMarginDirection: VerticalMarginDirection;
  size: Indent;
  children: ReactNode;
};

const getVerticalMarginClassName = (
  verticalMarginDirection: VerticalMarginDirection,
  size: Indent
) => {
  return `${verticalMarginDirection}${size[0].toUpperCase() + size.slice(1)}`;
};

export const IndentWrapper: FC<IndentWrapperProps> = ({
  verticalMarginDirection = 'both',
  size = 'l',
  children,
}) => {
  return (
    <div
      className={css[getVerticalMarginClassName(verticalMarginDirection, size)]}
    >
      {children}
    </div>
  );
};
