import React, { FC } from 'react';
import ReactDOM from 'react-dom';
import FocusTrap from 'focus-trap-react';
import cn from 'classnames';

import { useEscape } from './hooks/useKeypress';
import { Heading2 } from './Typography';
import { Button } from './Button';
import { IconButton } from './IconButton';
import { TextMarkdown } from './Typography';

import css from './Popup.css';
import { IconLoader } from './IconLoader';

const modalRoot = document.createElement('div');
document.body.appendChild(modalRoot);

export type PopupProps = {
  title?: string;
  description?: string;
  className?: string;
  classNameBody?: string;
  onClose?: (
    e: React.MouseEvent<HTMLDivElement | HTMLButtonElement, MouseEvent>
  ) => void;
  onAccept?: (
    e: React.MouseEvent<HTMLDivElement | HTMLButtonElement, MouseEvent>
  ) => void;
  onCancel?: (
    e: React.MouseEvent<HTMLDivElement | HTMLButtonElement, MouseEvent>
  ) => void;
  isWrapClosing?: boolean;
  children?: React.ReactNode;
  ariaLabel?: string;
  buttonAcceptTitle?: string;
  buttonCancelTitle?: string;
  buttonCloseTitle?: string;
};

export const Popup: FC<PopupProps> = ({
  title,
  description,
  className = '',
  classNameBody = '',
  onClose,
  onAccept,
  onCancel,
  isWrapClosing = true,
  children,
  ariaLabel,
  buttonCloseTitle = '',
  buttonAcceptTitle = '',
  buttonCancelTitle = '',
}) => {
  const [isMounted, setMounted] = React.useState(false);

  const el = React.useRef<HTMLDivElement>();

  React.useLayoutEffect(() => {
    document.body.style.overflow = 'hidden';
    const div = document.createElement('div');
    div.classList.add('WrapPopup');
    div.tabIndex = 0;
    div.setAttribute('aria-label', 'popup');

    const prevPopup = modalRoot.querySelector('.WrapPopup:last-child');

    if (prevPopup && prevPopup instanceof window.HTMLDivElement) {
      prevPopup.style.display = 'none';
    }

    modalRoot.appendChild(div);
    el.current = div;
    setMounted(true);

    return () => {
      if (modalRoot.contains(div)) {
        modalRoot.removeChild(div);
      }

      if (prevPopup && prevPopup instanceof window.HTMLDivElement) {
        prevPopup.style.display = '';
      } else {
        document.body.style.overflow = '';
      }
    };
  }, []);

  const handleClose = React.useCallback(
    (e) => {
      if (el?.current?.style?.display !== 'none') {
        onClose?.(e);
      }
    },
    [onClose]
  );

  const handleWrapClick = React.useCallback(
    (e) => {
      if (isWrapClosing) {
        handleClose(e);
      }
    },
    [handleClose, isWrapClosing]
  );

  const isShowActions =
    (onAccept && buttonAcceptTitle) || (onCancel && buttonCancelTitle);

  const isEmpty = !isShowActions && !description && !children;

  useEscape(handleClose);

  return isMounted && el.current
    ? ReactDOM.createPortal(
        <FocusTrap>
          <div className={css.wrapper}>
            <div
              className={css.background}
              aria-hidden
              tabIndex={-1}
              onClick={handleWrapClick}
            />
            <div
              tabIndex={0}
              role="dialog"
              aria-label={ariaLabel}
              className={cn(css.container, className)}
            >
              {onClose && (
                <span className={css.buttonClose}>
                  <IconButton
                    type="button"
                    tabIndex={0}
                    size="sm"
                    aria-label={buttonCloseTitle}
                    onClick={handleClose}
                  >
                    <IconLoader
                      name="icon:core/common/ic-24-cross"
                      size="sm"
                      color="greyDark"
                    />
                  </IconButton>
                </span>
              )}
              {title && (
                <div
                  className={cn(
                    css.header,
                    onClose && css.hasClose,
                    isEmpty && css.indentLg
                  )}
                >
                  <div className={css.title}>
                    <Heading2>{title}</Heading2>
                  </div>
                </div>
              )}
              {(description || children) && (
                <div
                  className={cn(
                    css.body,
                    classNameBody,
                    !isShowActions && css.indentLg
                  )}
                >
                  {description && (
                    <TextMarkdown color="greyDark">{description}</TextMarkdown>
                  )}
                  {children}
                </div>
              )}
              {isShowActions && (
                <div className={css.actions}>
                  {onAccept && buttonAcceptTitle && (
                    <div className={css.actionsButton}>
                      <Button theme="primary" onClick={onAccept}>
                        {buttonAcceptTitle}
                      </Button>
                    </div>
                  )}

                  {onCancel && buttonCancelTitle && (
                    <div className={css.actionsButton}>
                      <Button theme="secondary" onClick={onCancel}>
                        {buttonCancelTitle}
                      </Button>
                    </div>
                  )}
                </div>
              )}
            </div>
          </div>
        </FocusTrap>,
        el.current
      )
    : null;
};
