import React, {
  useCallback,
  useLayoutEffect,
  useRef,
  useState,
  ClipboardEvent,
} from 'react';
import cn from 'classnames';

import { Caption } from './Typography';
import { MarkdownComponent } from './Markdown';
import { IndentWrapper } from './Indent';
import { useReadOtp } from './hooks/useReadOtp';

import inputStyle from './Input.css';
import smsStyle from './ConfirmCodeInput.css';

const replaceAt = (
  value: string,
  index: number,
  replacement: string
): string => {
  const paddedValue = value.padEnd(Math.max(index + 1, value.length));
  return `${paddedValue.substr(0, index)}${replacement}${paddedValue.substr(
    index + 1,
    paddedValue.length
  )}`.replace(/\s+$/g, '');
};

const BACKSPACE_KEY_CODE = 8;
const ENTER_KEY_CODE = 46;
const LEFT_ARROW_KEY_CODE = 37;
const RIGHT_ARROW_KEY_CODE = 39;

type ActionPosition = 'top' | 'bottom';

export interface ConfirmCodeInputProps {
  onChange: (arg0: string) => void;
  onComplete: () => void;
  onBlur?: (arg0?: React.SyntheticEvent<HTMLInputElement>) => void;
  name: string;
  value: string;
  label: string;
  inputCount?: number;
  initInFocus?: boolean;
  disabled?: boolean;
  error?: string;
  description?: string | React.ReactNode;
  action?: React.ReactNode;
  actionPosition?: ActionPosition;
  autoComplete?: string;
  webOtpApiEnabled?: boolean;
}

export const ConfirmCodeInput = ({
  onChange,
  onComplete = () => {},
  onBlur = () => {},
  name,
  value,
  label,
  inputCount = 5,
  disabled = false,
  initInFocus = true,
  error = '',
  description,
  action,
  actionPosition = 'bottom',
  autoComplete = 'off',
  webOtpApiEnabled = false,
}: ConfirmCodeInputProps) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isFocusable, setIsFocusabe] = useState(
    Array.from({ length: inputCount }).map(() => false)
  );
  const inputs = useRef<Array<HTMLInputElement | null>>([]);

  const handleOnComplite = useCallback(
    (value: string): void => {
      if (value?.replace(/\s+/g, '')?.length === inputCount) {
        onComplete();
      }
    },
    [onComplete, inputCount]
  );

  const handleInputChange = useCallback(
    ({ target }: React.ChangeEvent<HTMLInputElement>) => {
      let newChar = target.value.replace(/\s/g, '');
      newChar = newChar.substr(newChar.length - 1, 1);

      if (/\d/g.test(newChar)) {
        const outputValue = replaceAt(value, currentIndex, newChar);

        onChange(outputValue);

        if (currentIndex < inputCount - 1) {
          const nextIndex = currentIndex + 1;
          inputs?.current[nextIndex]?.focus();
          setCurrentIndex(nextIndex);
        } else {
          inputs?.current[currentIndex]?.blur();
        }

        handleOnComplite(outputValue);
      }
    },
    [onChange, currentIndex, inputs, value, inputCount, handleOnComplite]
  );

  const handleFocus = useCallback(
    (event: React.SyntheticEvent<HTMLInputElement>) => {
      const { currentTarget } = event;

      const inputIndex = currentTarget.dataset.index;
      if (Number(inputIndex) !== currentIndex) {
        setCurrentIndex(Number(inputIndex));

        if (currentTarget.value.trim()) {
          inputs?.current[Number(inputIndex)]?.select();
        }
      }
    },
    [currentIndex, inputs, setCurrentIndex]
  );

  const handleBlur = (event: React.SyntheticEvent<HTMLInputElement>) => {
    setIsFocusabe(isFocusable.map(() => false));
    onBlur(event);
  };

  const handleKeyDown = useCallback(
    (event: React.KeyboardEvent<HTMLInputElement>) => {
      const clearKeyKodes = [BACKSPACE_KEY_CODE, ENTER_KEY_CODE];
      const { index } = event.currentTarget.dataset;

      if (clearKeyKodes.includes(event.keyCode)) {
        event.preventDefault();
        const outputValue = replaceAt(value, currentIndex, ' ');
        onChange(outputValue);

        if (Number(index) > 0 && !value[Number(index)]) {
          const nextIndex = +currentIndex - 1;
          inputs?.current[nextIndex]?.focus();
          const nextValue = replaceAt(value, nextIndex, ' ');
          onChange(nextValue);
          setCurrentIndex(nextIndex);
        }
      }

      if (event.keyCode === LEFT_ARROW_KEY_CODE && currentIndex > 0) {
        event.preventDefault();
        const nextIndex = +currentIndex - 1;
        inputs?.current[nextIndex]?.focus();
        setCurrentIndex(nextIndex);
      }

      if (
        event.keyCode === RIGHT_ARROW_KEY_CODE &&
        currentIndex < inputCount - 1
      ) {
        event.preventDefault();

        const nextIndex = +currentIndex + 1;
        inputs?.current[nextIndex]?.focus();
        setCurrentIndex(nextIndex);
      }
    },
    [currentIndex, value, onChange, inputs, setCurrentIndex, inputCount]
  );

  useLayoutEffect(() => {
    if (initInFocus && inputs) {
      inputs?.current[currentIndex]?.focus();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handlePaste = useCallback(
    (event: ClipboardEvent<HTMLInputElement>) => {
      event.preventDefault();

      if (disabled) {
        return;
      }

      const pastedData = event.clipboardData
        .getData('text/plain')
        .replace(/\D+/g, '')
        .slice(0, inputCount - currentIndex);

      if (pastedData.length === 0) {
        return;
      }

      const nextIndex =
        currentIndex + pastedData.length > inputCount - 1
          ? inputCount - 1
          : currentIndex + pastedData.length;
      const outputValue = replaceAt(
        value.slice(0, currentIndex),
        currentIndex,
        pastedData
      );

      setCurrentIndex(nextIndex);
      inputs?.current[nextIndex]?.focus();

      onChange(outputValue);

      handleOnComplite(outputValue);
    },
    [currentIndex, disabled, inputCount, value, onChange, handleOnComplite]
  );

  const handleReadOtp = (code: string) => {
    if (code.length === 0) {
      return;
    }

    const nextIndex =
      currentIndex + code.length > inputCount - 1
        ? inputCount - 1
        : currentIndex + code.length;
    const outputValue = replaceAt(
      value.slice(0, currentIndex),
      currentIndex,
      code
    );

    setCurrentIndex(nextIndex);
    inputs?.current[nextIndex]?.focus();

    onChange(outputValue);

    handleOnComplite(outputValue);
  };


  useReadOtp(handleReadOtp, {
    enabled: webOtpApiEnabled && !disabled && !value,
  });

  return (
    <label className={smsStyle.field}>
      <div className={cn(smsStyle.inputBlock, error && smsStyle.error)}>
        <div className={smsStyle.header}>
          <div className={inputStyle.label}>{label}</div>
          {action && actionPosition === 'top' && (
            <div className={smsStyle.action}>{action}</div>
          )}
        </div>
        <div className={smsStyle.inputs}>
          {Array.from({ length: inputCount }).map((_: any, index: number) => (
            <input
              key={`${name}-${index}`}
              name={`${name}${index}`}
              className={cn(inputStyle.input, smsStyle.input)}
              ref={(el) => (inputs.current[index] = el)}
              data-index={index}
              value={value[index] || ''}
              onInput={handleInputChange}
              onChange={() => {}}
              onKeyDown={handleKeyDown}
              onFocus={handleFocus}
              onBlur={handleBlur}
              onPaste={handlePaste}
              pattern="[0-9]"
              inputMode="numeric"
              autoComplete={index === 0 ? autoComplete : 'off'}
              disabled={disabled}
            />
          ))}
        </div>
      </div>
      {error && (
        <Caption className={smsStyle.caption} color="orange">
          <MarkdownComponent linkColor="orange">{error}</MarkdownComponent>
        </Caption>
      )}
      {description &&
        (typeof description === 'string' ? (
          <Caption className={smsStyle.caption} color="greyDark">
            <MarkdownComponent>{description}</MarkdownComponent>
          </Caption>
        ) : (
          description
        ))}
      {action && actionPosition === 'bottom' && (
        <IndentWrapper verticalMarginDirection="top" size="s">
          {action}
        </IndentWrapper>
      )}
    </label>
  );
};
