import React, { useCallback, useEffect, useState } from 'react';
import Calendar, { CalendarProps } from 'react-calendar';
import cn from 'classnames';
import dateFnsFormat from 'date-fns/format';
import dateFnsParse from 'date-fns/parse';
import dateFnsIsValid from 'date-fns/isValid';
import ru from 'date-fns/locale/ru';

import { Input } from './Input';
import { InputMasked, InputMaskedProps } from './InputMasked';
import { IconButton } from './IconButton';
import { IconLoader } from './IconLoader';

import { useOutClick } from './hooks/useOutside';
import { useEscape } from './hooks/useKeypress';

import { ReactComponent as IconCalendarPrev } from './icons/calendarPrev.svg';
import { ReactComponent as IconCalendarPrev2 } from './icons/calendarPrev2.svg';

import css from './InputDate.css';

type InputDatePosition = 'bottom' | 'top';

export type InputDateProps = Omit<InputMaskedProps, 'options' | 'onChange'> & {
  onChange: (date: string) => void;
  isMobile?: boolean;
  maskedOptions?: InputMaskedProps['options'];
  calendarMinDate?: Date;
  calendarMaxDate?: Date;
  calendarActiveStartDate?: Date;
  calendarDeafaultActiveStartDate?: Date;
  position?: InputDatePosition;
  locale?: string;
  calendarButtonTitle?: string;
  calendarPrevAriaLabel?: string;
  calendarPrev2AriaLabel?: string;
  calendarNextAriaLabel?: string;
  calendarNext2AriaLabel?: string;
};

const defaultMaskedOptions = {
  mask: Date,
  overwrite: true,
  autofix: true,
};

const PrevLabel = ({ className }: { className?: string }) => (
  <div className={css.focusTab}>
    <IconCalendarPrev className={className} />
  </div>
);

const NextLabel = () => <PrevLabel className={css.nextLabel} />;

const Prev2Label = ({ className }: { className?: string }) => (
  <div className={css.focusTab}>
    <IconCalendarPrev2 className={className} />
  </div>
);

const Next2Label = () => <Prev2Label className={css.next2Label} />;

const formatMonthYear: CalendarProps['formatMonthYear'] = (_locale, date) =>
  dateFnsFormat(date, 'LLLL y', { locale: ru });

export const InputDate = ({
  isMobile,
  onChange,
  value = '',
  maskedOptions = defaultMaskedOptions,
  calendarMinDate,
  calendarMaxDate,
  calendarActiveStartDate,
  calendarDeafaultActiveStartDate,
  position = 'bottom',
  locale = 'ru-RU',
  calendarButtonTitle = '',
  calendarPrevAriaLabel = '',
  calendarPrev2AriaLabel = '',
  calendarNextAriaLabel = '',
  calendarNext2AriaLabel = '',
  inputRef,
  ...props
}: InputDateProps) => {
  const [isShownCalendar, setIsShownCalendar] = React.useState(false);
  const [side, setSide] = useState<'left' | 'right'>('right');
  const wrapperRef = React.useRef<HTMLDivElement>(null);
  const wrapperInputRef = React.useRef<HTMLDivElement>(null);
  const ownInputRef = React.useRef<HTMLInputElement>(null);
  const localInputRef = inputRef || ownInputRef;

  const dateFromMasked = dateFnsParse(value, 'dd.MM.yyyy', Date.now());

  const defaultActiveStartDate = React.useMemo(() => {
    if (value && dateFnsIsValid(dateFromMasked)) {
      if (calendarMinDate && calendarMaxDate) {
        if (
          dateFromMasked.getTime() > calendarMinDate.getTime() &&
          dateFromMasked.getTime() < calendarMaxDate.getTime()
        ) {
          return dateFromMasked;
        } else return calendarDeafaultActiveStartDate;
      } else return dateFromMasked;
    } else {
      return calendarDeafaultActiveStartDate;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [value, dateFromMasked]);

  useEffect(() => {
    if (wrapperInputRef.current) {
      if (wrapperInputRef.current.clientWidth < 300) {
        setSide('left');
      }
    }
  }, []);

  const handleInputMasked = React.useCallback(
    (_unmaskedValue: string, currentValue: string) => {
      if (currentValue !== value) {
        onChange(currentValue);
      }
    },
    [onChange, value]
  );

  const handleInputChange = React.useCallback(
    (e) => {
      onChange(
        e.target.value
          ? dateFnsFormat(
              dateFnsParse(e.target.value, 'yyyy-MM-dd', Date.now()),
              'dd.MM.yyyy'
            )
          : ''
      );
    },
    [onChange]
  );

  const handleCalendarClick = React.useCallback(() => {
    setIsShownCalendar(true);
  }, []);

  const handleCalendarClose = React.useCallback(() => {
    setIsShownCalendar(false);
    localInputRef?.current?.focus();
  }, [localInputRef]);

  const handleCalendarChange = React.useCallback(
    (val) => {
      onChange(dateFnsFormat(val, 'dd.MM.yyyy'));
      handleCalendarClose();
    },
    [onChange, handleCalendarClose]
  );

  const handleClickOut = useCallback(() => {
    if (isShownCalendar) {
      handleCalendarClose();
    }
  }, [isShownCalendar, handleCalendarClose]);

  useOutClick(isShownCalendar, wrapperRef, handleClickOut);
  useEscape(handleClickOut);

  return isMobile ? (
    <Input {...props} type="date" onChange={handleInputChange} />
  ) : (
    <div ref={wrapperInputRef} className={css.wrapDatePicker}>
      <InputMasked
        {...props}
        inputRef={localInputRef}
        value={value}
        onChange={handleInputMasked}
        options={maskedOptions}
        icon={
          !props.disabled && (
            <IconButton
              type="button"
              tabIndex={0}
              size="md"
              onClick={handleCalendarClick}
              aria-label={calendarButtonTitle}
            >
              <IconLoader
                name="icon:core/common/ic-24-calendar"
                color="greyDark"
                size="md"
              />
            </IconButton>
          )
        }
      />
      {isShownCalendar && (
        <div ref={wrapperRef}>
          <Calendar
            prevLabel={<PrevLabel />}
            prevAriaLabel={calendarPrevAriaLabel}
            prev2AriaLabel={calendarPrev2AriaLabel}
            nextAriaLabel={calendarNextAriaLabel}
            next2AriaLabel={calendarNext2AriaLabel}
            nextLabel={<NextLabel />}
            prev2Label={<Prev2Label />}
            next2Label={<Next2Label />}
            className={cn(css.wrapCalendar, css[position], css[side])}
            onChange={handleCalendarChange}
            value={
              (value && dateFnsIsValid(dateFromMasked)
                ? dateFromMasked
                : null) || calendarMinDate
            }
            minDate={calendarMinDate}
            maxDate={calendarMaxDate}
            activeStartDate={calendarActiveStartDate}
            defaultActiveStartDate={defaultActiveStartDate}
            locale={locale}
            formatMonthYear={formatMonthYear}
          />
        </div>
      )}
    </div>
  );
};
